import React, { Component } from "react";
import { VscChevronRight } from "react-icons/vsc";

export default class Button extends Component {
  render() {
    return (
      <button className="btn backdrop">
        {this.props.text}
        <span className="icon">
          <VscChevronRight />
        </span>
      </button>
    );
  }
}
