import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import Button from "./Button";

export default class Intro extends Component {
  render() {
    return (
      <div>
        <br />
        <br />
        <br />
        <div className="introHead"> I, develop. </div>
        <div className="introSub">
          My name is <strong>akshay</strong> and this is my showcase. You're
          welcome.
        </div>
        <NavLink to="/works">
          <Button text="works" />
        </NavLink>
        <NavLink to="/extra">
          <Button text="extra" />
        </NavLink>
        <br />
        <br />
      </div>
    );
  }
}
