import React, { Component } from "react";
import Box from "./Box";

export default class Boxes extends Component {
  render() {
    return (
      <div className="boxes">
        <Box
          text="Works"
          text2="I work on showcasing my skills in defined ways. Here are 
        few works that I've previously done to improve/better my 
        skills and abilities."
        />
        <Box
          text="Blog"
          text2="If it works, it works, bitch."
          items={[
            "Article #1022 🤣😍",
            "How to manage level one tasks in firebase with props",
            "the next aricle is fire babyy"
          ]}
        />
        <Box text="Interests" text2="No one tells me what to type." />
        <Box text="Contact" text2="Hell yeh." icon="VscArrowRight" />
      </div>
    );
  }
}
