import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import Button from "./elements/ButtonNav";

export default class Navigation extends Component {
  render() {
    return (
      <div className="navbar backdropNav">
        <NavLink to="/" exact>
          <Button text="home" />
        </NavLink>
        <NavLink to="/works">
          <Button text="works" />
        </NavLink>
        <NavLink to="/extra">
          <Button text="extra" />
        </NavLink>
      </div>
    );
  }
}
